:root {
  --background-color-primary: #101010;
  --background-color-secundary-: #191919;
  --color-text-primary: #FFFFFF;
  --color-text-secondary: #9396A0;
  --button-background: #F9004D;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
h2 {
  font-size: 4rem;
  color: var(--color-text-primary);
  font-weight: 700;
  padding: 0.5rem 0;
}
p {
  font-size: 1.2rem;
  color: var(--color-text-secondary);
}
.appWidth {
  width: 100%;
  height: 100%;
}
/* NAVBAR */
.navbar {
  width: 100%;
  background-color: var(--background-color-primary);
  height: 6rem;
  padding: 0 3rem;
}
.containerNavBar {
  max-width: 1280px;
  width: 100%;
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbarLogo {
  width: 10rem;
  height: 100%;
  display: flex;
  align-items: center;
}
.navbarLogo img {
  width: 100%;
}
.containerNavBar ul {
  display: flex;
  align-items: center;
}
.containerNavBar ul li{
  list-style: none;
  padding-right: 2rem;
}
.containerNavBar .lasItem {
  border: 2px solid #C6C9D8;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
}
.containerNavBar ul li a{
  text-decoration: none;
  color: #C6C9D8;
  font-weight: 500;
  transition:0.3s;
}
.containerNavBar ul li a:hover{
  text-decoration: none;
  color: #F11046;
  font-weight: 500;
}
.hamburguerContainer {
  display: none;
}
.hamburguerButton {
  width: 30px;
  height: 30px;
  color: var(--color-text-primary);
}

/* SIDEBAR */
.backgroundContainer {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
}
.containerSideBar {
  background-color: red;
  width: 60%;
  height: 100%;
}
.activeSideBar {
  display: block;
}
.inactiveSideBar {
  display: none;
}

/* HEADER */
.header {
  width: 100%;
  height: 100%;
  background-color: var(--background-color-primary);
  padding: 2rem 3rem;
  padding-bottom: 9rem;
}
.header-container {
  width: 100%;
  max-width: 1280px;
  margin: auto;
}
.header-container h1 {
  font-size: 7rem;
  font-weight: 800;
  background: #F10E52;
  background: -webkit-linear-gradient(to top right, #F10E52 0%, #F71C12 100%);
  background: -moz-linear-gradient(to top right, #F10E52 0%, #F71C12 100%);
  background: linear-gradient(to top right, #F10E52 0%, #F71C12 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.itemService {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 1rem;
  margin-top: 1.8rem;
}
.itemService-container {
  max-width: 25rem;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.itemService-container h3 {
  color: var(--color-text-primary);
  font-weight: 400;
  font-size: 1.6rem;
  margin-top: 1rem;
}
.itemService-container p {
  color: var(--color-text-secondary);
  font-size: 1.1rem;
  margin-top: 1rem;
}

/* ABOUT */
.about {
  width: 100%;
  background-color: var(--background-color-secundary-);
  padding: 0 3rem;
  padding-bottom: 5rem;
}
.about-container {
  margin: auto;
  width: 100%;
  max-width: 1280px;
  display: flex;
}
.about-container-img {
  width: 38%;
}
.about-container-img img {
  margin-top: -5rem;
  width: 100%;
  object-fit: cover;
  border-radius: 0.8rem;
}
.about-container-text {  
  width: 62%;
  display: flex;
  flex-direction: column;
  padding: 6rem 4rem;
}
.about-container-text h3 {
  color: var(--color-text-primary);
  font-weight: 700;
  font-size: 5rem;
}
.about-container-text p {
  font-weight: 400;
  color: var(--color-text-secondary);
  font-size: 1.1rem;
  line-height: 2rem;
}
.about-container-text-items{
  margin-top: 2rem;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 0.5rem;
}
.itemAbout {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.itemAbout h4 {
  color: var(--color-text-primary);
  font-size: 2.5rem;
}
.itemAbout p {
  color: var(--color-text-secondary);
  font-size: 1.1rem;
}

/* SERVICE */
.service {
  width: 100%;
  background-color: var(--background-color-primary);
  padding: 5rem 3rem;
}
.service-container {
  width: 100%;
  max-width: 1280px;
  margin: auto;
  display: flex;
}
.service-container__title {
  width: 35%;
  display: flex;
  flex-direction: column;
  padding-right: 1rem;
}
.service-container__title h4{
  color: var(--color-text-primary);
  font-size: 4rem;
}
.service-container__title p{
  color: var(--color-text-secondary);
  padding-top: 0.5rem;
  font-size: 1.2rem;
}
.service-container__title button{
  margin-top: 1.5rem;
  border: none;
  background-color: transparent;
  text-align: left;
  color: var(--color-text-primary);
  font-size: 1rem;
  font-weight: 600;
}
.service-container__title button:hover ~ .underlineButton {
  width: 14rem;
}
.underlineButton {
  margin-top: 0.5rem;
  width: 2rem;
  height: 2px;
  background: rgb(247,28,18);
  background: linear-gradient(90deg, rgba(247,28,18,1) -23%, rgba(241,14,82,1) 0%, rgba(247,28,18,1) 100%);
  transition: 0.5s;
}
.service-container__services {
  width: 65%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 1rem;
  grid-row-gap: 3rem
}
.itemModify {
  width: 100%;    
  border-radius: 3rem;
  transition: 0.3s;
  padding: 2rem;
  position: relative;
  z-index: 1;
  /* background: transparent; */
}
.itemModify::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgb(247,28,18);
  background: linear-gradient(90deg, rgba(247,28,18,1) -23%, rgba(241,14,82,1) 0%, rgba(247,28,18,1) 100%);
  z-index: -1;
  transition: opacity 0.2s linear;
  opacity: 0;
  border-radius: 3rem;
}
.itemModify:hover::before {
  opacity: 1;
}
.itemModify-icon {
  width: 100%;  
  color: white;
}
.svgService {
  width: 330px;
}
.itemModify h3 {
  color: var(--color-text-primary);
  font-size: 1.4rem;
  font-weight: 500;
  margin: 1rem 0;
}
.itemModify p {
  color: var(--color-text-secondary);
}

/* OUR WORKS */
.works {
  width: 100%;
  background-color: var(--background-color-secundary-);
  position: relative;
}
.works-container {
  width: 100%;
  /* max-width: 1280px; */
  margin: auto;
  padding-bottom: 2rem;
}
.works-container__text {
  padding: 3rem;
  width: 50%;
}
.works-container__container-slider {
  position: relative;
  width: 100%;
  height: 100%;  
  overflow: hidden;
}
.works-container__slider {
  padding-top: 5rem;
  padding-bottom: 5rem;
  width: 112%; 
  display: flex;
  margin-left: -12%;
  gap: 1%;  
  background: var(--background-color-secundary-);
}
.itemWork {
  min-width: 25rem;
  width: 27%;
  min-height: 80vh;
  position: relative;
  border-radius: 0.5rem;
  overflow: hidden;
}
.itemWork__content::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgb(247,28,18);
  background: linear-gradient(-225deg, rgba(247,28,18,1) -23%, rgba(241,14,82,1) 0%, rgba(247,28,18,0.1) 80%);
  z-index: -1;
  transition: opacity 0.2s linear;
  opacity: 0;
  border-radius: 0.5rem;
}
.itemWork__content:hover::before {
  opacity: 1;
  z-index: -1;
}
.itemWork__image {  
  position: absolute;
  width: 100%;
  height: 100%;
}
.itemWork__image img {
  width: 100%;
  height: 100%;
}
.itemWork__content {
  position: relative;  
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 1rem;
  padding-bottom: 3rem;
  transition: 500ms;
  gap: 0.8rem;
  z-index: 10;
}
.itemWork__content p { 
  font-size: 0.9rem;
  font-weight: 500;
}
.itemWork__content h3 { 
  color: var(--color-text-primary);
  font-weight: 600;
  font-size: 1.3rem;
}
.itemWork__content-Button{
  display: flex;
  margin-top: 1.5rem;
}
.works-container__facts h3{
  color: var(--color-text-primary);
  font-size: 2.2rem;
  font-weight: 500;
  text-align: center;
}
.works-facts__container {
  display: flex;
  gap: 5rem;
  width: 100%;
  max-width: 1280px;
  margin: auto;
  padding: 0 3rem;
}
.itemContainer {
  width: 100%;
  flex: 1 1 0%;
  text-align: center;
}
.itemContainer-text {
  display: flex;
  justify-content: center;
  color: var(--button-background);
}
.itemContainer-text h3{
  color: var(--button-background);
  font-size: 4.5rem;
  font-weight: 600;
  padding: 0;
  margin: 0;
}
.itemContainer-text span{
  font-size: 2rem;
  margin-top: 0.6rem;
  font-weight: 600;
}
.caseButton{
  padding: 0.5rem 1.2rem;
  border: 1px solid var(--color-text-secondary);
  border-radius: 0.3rem;
  color: var(--color-text-primary);
  font-weight: 500;
  font-size: 0.8rem;
  transition: all 500ms;
}
.caseButton:hover{
  background-color: var(--button-background);
  border: 1px solid transparent;
  transition: background-color 500ms;
  transition: transform 500ms ; 
  transform: translateY(-5px);
}

/* Team Section */
.team {
  background-color: var(--background-color-primary);
  width: 100%;
  padding: 2rem 0;
}
.team-container {
  width: 100%;
  max-width: 1280px;
  margin: auto;
  text-align: center;
}
.itemText-container {
  width: 100%;
  color: var(--color-text-primary);
  font-size: 2rem;
  font-weight: 500;
  display: none;
}
.showTeamText {
  display: block;
}
.iteam-container {
  width: 100%;
}
.itemText-container h3 {
  color: var(--color-text-primary);
  font-size: 2rem;
  font-weight: 500;
  padding: 0 8rem;
  padding-top: 3.5rem;
  padding-bottom: 5.5rem;
  line-height: 3.6rem;
}
.itemText-container p {
  color: var(--color-text-primary);
}
.itemText-container p span {
  color: var(--color-text-secondary);
}
.team-container__images {
  width: 100%;
  max-width: 750px;
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  margin: auto;
  justify-content: center;
  padding: 2rem 0;
}
.itemImage-container {
  width: 95px;
  height: 85px;
  position: relative;
  transition:all 320ms;
}
.itemImage-container:hover {
  transform: translateY(-10px);
  transform: scale(1.1);
}
.itemImage-container img{
  border-radius: 0.5rem;
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;  
}
.activeItemImage {
  width: 1rem;
  height: 1rem;
  border-radius: 10rem;
  position: absolute;
  top: -0.4rem;
  right: -0.4rem;
  background-color: var(--button-background);
}

/* News Section*/
.news {
  background-color: var(--background-color-secundary-);
}
.news-container {
  width: 100%;
  max-width: 1280px;
  margin: auto;
  padding: 3rem 3rem;
  padding-top: 6rem;
}
.news-container__text{
  width: 50%;
}
.news-container__allnews {
  width: 100%;
  display: flex;
  gap: 1.4rem;
  padding: 2rem 0;
}
.itemNews {
  width: 100%;
  max-width: 23rem;
  border-radius: 2rem;
  height: 32rem;
  position: relative;
  overflow: hidden;
  transition: all 1s;
}
.itemNews:hover  .itemNews-text {
  transform: translateY(-6rem);
}
.itemNews:hover .itemNews-background::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  object-position: center;
  -webkit-transition: all 3s;
  transition: all 3s;
  background: rgb(247,28,18);
  background: linear-gradient(-225deg, rgba(247,28,18,1) -23%, rgba(241,14,82,1) 0%, rgba(247,28,18,0.1) 80%);
}
.itemNews-background {
  position: absolute;
  width: 100%;
  max-width: 23rem;
  height: 32rem;
  z-index: 0;
  transition: all 0.5s linear;
}
.itemNews-background::after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  background: rgb(0,0,0);
  background: linear-gradient(0deg, rgba(0,0,0,1) -23%, rgba(29,29,29,0) 87%);
  width: 100%;
  max-width: 23rem;
  height: 32rem;
  z-index: 2;
  transition: all 0.2s linear;
}
.itemNews-background img{
 object-fit: cover;
 object-position: center;
 width: 100%;
 height: 100%;
}
.itemNews-text {
  position: relative;
  z-index: 2;
  padding: 0 2.5rem;
  padding-top: 21.5rem;
  transition: all 0.5s;
}
.itemNews-text p {
  font-size: 0.95rem;
  font-weight: 600;
  margin-bottom: 0.6rem;
}
.itemNews-text h3{
  color: var(--color-text-primary);
  font-weight: 500;
  font-size: 1.6rem;
}
.itemNews-text button{
  padding: 0.5rem 1.3rem;
  border-radius: 0.5rem;
  background-color: transparent;
  border: 2px solid var(--button-background);
  color: var(--color-text-primary);
  font-size: 0.9rem;
  text-transform: uppercase;
  margin-top: 4rem;
  transition: all 0.3s;
  cursor: pointer;
}
.itemNews-text button:hover {
  background-color: var(--button-background);
  transform: translateY(-0.5rem);
}

/* Footer Section */
.footer {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: var(--background-color-primary);
  overflow: hidden;
}
.footer-contact {
  width: 100%;
  background: rgb(247,28,18);
  background: linear-gradient(-225deg, rgba(247,28,18,1) -23%, rgba(241,14,82,1) 0%, rgba(202, 14, 39, 0.8) 80%);
  margin-top:-2rem;
  border-radius: 1rem 1rem 0 0;  
  padding: 6rem;
  position: relative;
  overflow: hidden;
  z-index: 200;
}
.footer-contact::after {  
  position: absolute;
  background: url(http://trydo.rainbowit.net/assets/images/logo/big-logo.png);
  content: '';
  top: 30%;
  right: -5rem;
  width: 25rem;
  height: 18rem;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: -1;
}
.footer-contact p{
  color: var(--color-text-primary);
  text-transform: uppercase;
  font-size: 1.1rem;
  letter-spacing: 0.5rem;
}
.footer-contact h2{
  font-weight: 900;
  font-size: 4.5rem;
}
.footer-contact button{ 
  padding: 0.85rem 3rem;
  margin-top: 2rem;
  color: var(--color-text-primary);
  background-color: transparent;
  border: 0.14rem solid var(--color-text-primary);
  border-radius: 0.5rem;
  font-size: 0.95rem;
  font-weight: 500;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  transition: all 0.5s;
}
.footer-contact button:hover {
  background-color: var(--color-text-primary);
  color: var(--button-background);
  transform: translateY(-0.5rem);
}
.footer-text {
  padding: 4rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
}
.footer-text p {
  font-size: 0.85rem;
}
.footer-text__links {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  color: var(--color-text-secondary);
  padding-bottom: 6rem;
}
.footer-text__links h4{
  font-size: 1.3rem;
  padding-bottom: 1rem;
  font-weight: 600;
}
.footer-text__links ul li{
  list-style: none;
  line-height: 2.5rem;
}
@media only screen and (max-width: 890px) {
  body {
    background-color: var(--background-color-primary);
  }
  .containerNavBar ul li{
    padding-right: 0.7rem;
  }
  .itemService {
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 1rem;
    grid-row-gap: 2rem;
  }
  .about-container {
    flex-wrap: wrap;
  }

  .about-container-img {
    padding: 0 2rem;
    width: 100%;
  }
  .about-container-text {
    width: 100%;
    padding: 0;
  }
  .about-container-text h3 {
    font-size: 4rem;
  }
  .about-container-text p {
    font-size: 1rem;
  }
  .about-container-text-items {
    grid-template-columns: 1fr;
    grid-row-gap: 3rem;
  }
  .about-container-text-items h4{
    font-size: 2rem;    
  }
  .service-container {
    display: inline-block;
  }
  .service-container__title {
    width: 100% ;
  }
  .service-container__services {
    width: 100%;
    grid-template-columns: 1fr;
    padding-top: 4rem;
  }
  .itemModify {
    background: rgb(247,28,18);
  background: linear-gradient(90deg, rgba(247,28,18,1) -23%, rgba(241,14,82,1) 0%, rgba(247,28,18,1) 100%);
  }
  .works-container__text {
    padding: 3rem;
    width: 100%;
  }
  .works-facts__container {
    display: grid;
    grid-template-columns: 1fr;
  }
  .team-container h3 {
    padding: 1rem 2rem;
    font-size: 1.3rem;
    line-height: normal;
    padding-bottom: 3rem;
  }
  .team-container p { 
    font-size: 1rem;
  }
  .news-container__allnews {
    flex-wrap: wrap;
  }
  .itemNews {
    width: 100%;
    max-width: 100%;
  }
  .itemNews-background {
    max-width: 100%;
  }
  .itemNews-background::after {
    max-width: 100%;
  }
  .footer {
    grid-template-columns: 1fr;
  }
  .footer-contact{
    padding: 2rem;
    padding-top: 4rem;
  } 
  .footer-text {
    padding-left: 3rem;
    padding-right: 3rem;
  }
  .footer-text__links {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .news-container {
    padding-left: 2rem;
    padding-right: 2rem;
  }
  .news-container__text{
    width: 100%;
  }
}


@media only screen and (max-width: 612px) {
  .navItems{
    display: none;
  }
  .hamburguerContainer {
    display: block;
  }
  .itemService {
    grid-template-columns: 1fr;
    grid-column-gap: 1rem;
    grid-row-gap: 2rem;
  }
  .header-container h1 {
    font-size: 4rem;
  }
}